.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  cursor: default;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  display: flex;
  position: relative;
}

.is-active, .nav__link:hover {
  color: #00acdc;
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
