  border-radius: 5px;
  width: 200px;
  padding: 0 16px;
  transition-duration: .3s;
}

.card__image {
  justify-content: center;
  display: flex;
}

.card__image-content {
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__info {
  color: #060b35;
  text-align: left;
  margin-top: 40px;
  margin-bottom: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  font-size: 10px;
  line-height: 14px;
  font-weight: inherit;
  align-items: center;
  margin-top: 4px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: inherit;
  text-align: left;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-block: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
